.App-header {
  height: 50px;
  pointer-events: none;
  line-height: 50px;
}
.title {
  line-height: 50px;
  float: right;
  text-align: center;
  padding-right: 20px;
}
.App-logo {
  height: 100%;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
